body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  border-radius: 100%;
  /* border: 2px solid aqua; */
  height: 40vmin;
  transition-property: height, border;
  transition-duration: 100ms;
  transition-delay: 0s;
}

.CatDiv {
  height: 50vmin;
  width: 50vmin;
  transition-property: height, border;
  transition-duration: 100ms;
  transition-delay: 0s;
}

.CatDiv:hover .App-logo{
  height: 50vmin;
  border-radius: 100%;
  box-shadow: 0 0 10px aqua;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 5s linear;
            animation: App-logo-spin infinite 5s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.transitionDiv {
  margin: 0 auto;
  opacity: 0;
  transition-property: opacity;
  transition-duration: 1s;
  transition-delay: 0s;
}
.transitionDiv:hover {
  opacity: 1
}

@-webkit-keyframes inAnimation {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  100% {
    opacity: 1;
    visibility: visible;
  }
}

@keyframes inAnimation {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  100% {
    opacity: 1;
    visibility: visible;
  }
}

@-webkit-keyframes outAnimation {
  0% {
    opacity: 1;
    visibility: visible;
  }
  100% {
    opacity: 0;
    visibility: hidden;
  }
}

@keyframes outAnimation {
  0% {
    opacity: 1;
    visibility: visible;
  }
  100% {
    opacity: 0;
    visibility: hidden;
  }
}

@-webkit-keyframes hoverCat {
  0% {
    height: 40vmin;
  }
  100% {
    height: 50vmin;
  }
}

@keyframes hoverCat {
  0% {
    height: 40vmin;
  }
  100% {
    height: 50vmin;
  }
}

@-webkit-keyframes unHoverCat {
  0% {
    height: 50vmin;
  }
  100% {
    height: 40vmin;
  }
}

@keyframes unHoverCat {
  0% {
    height: 50vmin;
  }
  100% {
    height: 40vmin;
  }
}
